import React from "react";
import Avatar from "./Avatar.jsx";

function Card(props) {
  return (
    <div className="card" key={props.contact.index}>
      <div className="top">
        <h2 className="name">{props.contact.name}</h2>
        <Avatar src={props.contact.src} alt={props.contact.alt} />
      </div>
      <div className="bottom">
        <p className="info">{props.contact.tel}</p>
        <p className="info">{props.contact.email}</p>
      </div>
    </div>
  );
}

export default Card;
